.benefits {
    padding: 112px 0;
    font-family: $PTMono-Regular;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-black;
    background: #ffffff url("") no-repeat center;
  
    @media (max-width: $vp-1024) {
      padding: 59px 0 82px;
    }
  
    @media (max-width: $vp-768) {
      padding: 37px 0 65px;
    }
  }
  
  .benefits__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .benefits__item {
    box-sizing: border-box;
    margin-bottom: 30px;
    padding: 39px 43px;
    display: block;
    width: 48.7%;
    min-height: 299px;
    border: 1px solid #ebebeb;
    border-left: 3px solid $basic-blue;
    background-color: #ffffff;
    transition: 0.3s;
  
    &:nth-last-child(-n+2) {
      margin-bottom: 0;
    }
  
    &:hover {
      background-color: $basic-blue;
      color: $color-default-white;
    }
  
    @media (max-width: $vp-1024) {
      padding: 24px 20px 20px 24px;
      width: 47.9%;
      min-height: auto;
    }
  
    @media (max-width: $vp-768) {
      margin-bottom: 20px;
      padding: 24px 20px 23px 24px;
      width: 100%;
  
      &:nth-last-child(-n+2) {
        margin-bottom: 20px;
      }
  
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  
  .benefits__title {
    margin: 15px 0 16px;
    position: relative;
    font-family: $RobotoCondensed-Regular;
    font-size: 24px;
    line-height: 34px;
    color: $basic-blue;
    transition: 0.3s;
  
    @media (max-width: $vp-1024) {
      margin: 0 0 9px;
    }
  
    @media (max-width: $vp-768) {
      margin: 6px 0 10px;
      font-size: 20px;
      line-height: 28px;
    }
  }
  
  .benefits__item:hover .benefits__title {
    color: $color-default-white;
  }
  
  .benefits__icon path {
    fill: $basic-blue;
    transition: 0.3s;
  }
  
  .benefits__item:hover .benefits__icon path {
    fill: #ffffff;
  }
  
  .benefits__content {
    margin-bottom: 105px;
  
    @media (max-width: $vp-1024) {
      margin-bottom: 57px;
    }
  }
  